import React from 'react';
import PropTypes from 'prop-types';

const TitlePages = ({ text }) => {
  const goBackHandler = () => {
    window.history.back();
  };

  return (
    <div className="title-pages">
      <div className="title-text-wrapper" style={{ flexGrow: 1 }}>
        <h1 className="title-pages-text">{text}</h1>
      </div>
      <div className="button-wrapper">
        <a href="#" onClick={goBackHandler} className="cta-btn cta-btn--back">
          Back
        </a>
      </div>
    </div>
  );
};

TitlePages.propTypes = {
  text: PropTypes.string.isRequired,
};

export default TitlePages;
