import React from 'react';

import '../../style/pages/_footerPages.scss';

const FooterPages = () => {
  const scrollToTop = () => {
    window.scrollTo({
      top: 0,
      behavior: 'smooth',
    });
  };

  return (
    <footer className="footer-pages">
      <span className="back-to-top" onClick={scrollToTop}>
        <i className="fa fa-angle-up fa-2x" aria-hidden="true" />
      </span>
    </footer>
  );
};

export default FooterPages;
