import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import { Link } from 'gatsby';
import Fade from 'react-reveal/Fade';
import Tilt from 'react-tilt';
import { Container, Row, Col } from 'react-bootstrap';
import ProjectImg from '../Image/ProjectImg';
import '../../style/pages/_showcaseProjects.scss';

const ShowcaseProjects = ({ projects }) => {
  const [isDesktop, setIsDesktop] = useState(false);
  const [isMobile, setIsMobile] = useState(false);

  useEffect(() => {
    if (window.innerWidth > 769) {
      setIsDesktop(true);
      setIsMobile(false);
    } else {
      setIsMobile(true);
      setIsDesktop(false);
    }
  }, []);

  return (
    <section className="showcase-projects">
      <Container fluid>
        <div className="showcase-project-wrapper">
          <Row className="showcase-row">
            {projects.map((project) => {
              const { title, info, info2, url, repo, img, id } = project;

              const imageLink = url || repo;

              return (
                <Col lg={4} sm={12} key={id} className="showcase-col">
                  <Fade bottom duration={1000} delay={500} distance="30px">
                    <div className="showcase-project-container">
                      <div className="showcase-project-wrapper__image showcase-project-wrapper__multi">
                        <a
                          href={imageLink || '#!'}
                          target="_blank"
                          aria-label="Project Link"
                          rel="noopener noreferrer"
                        >
                          <Tilt
                            options={{
                              reverse: false,
                              max: 8,
                              perspective: 1000,
                              scale: 1,
                              speed: 300,
                              transition: true,
                              axis: null,
                              reset: true,
                              easing: 'cubic-bezier(.03,.98,.52,.99)',
                            }}
                          >
                            <div data-tilt className="thumbnail rounded">
                              <ProjectImg alt={title} filename={img} />
                            </div>
                          </Tilt>
                        </a>
                      </div>
                      <div className="showcase-project-wrapper__text">
                        <h3
                          id="showcase-project-title"
                          className="showcase-project-wrapper__text-title"
                        >
                          {title || 'Project Title'}
                        </h3>
                        <div>
                          <p className="showcase-projects-info">
                            {info || 'Project description here...'}
                          </p>
                          <p className="showcase-projects-info2">{info2 || ''}</p>
                        </div>
                        <div className="showcase-button-container">
                          {url && (
                            <Link
                              to={url}
                              className="cta-btn cta-btn--hero"
                              target="_blank"
                              rel="noopener noreferrer"
                            >
                              See Live
                            </Link>
                          )}

                          {repo && (
                            <a
                              target="_blank"
                              rel="noopener noreferrer"
                              className="cta-btn text-color-main"
                              href={repo}
                            >
                              Source Code
                            </a>
                          )}
                        </div>
                      </div>
                    </div>
                  </Fade>
                </Col>
              );
            })}
          </Row>
        </div>
      </Container>
    </section>
  );
};

ShowcaseProjects.propTypes = {
  projects: PropTypes.arrayOf(
    PropTypes.shape({
      title: PropTypes.string.isRequired,
      info: PropTypes.string,
      info2: PropTypes.string,
      url: PropTypes.string,
      repo: PropTypes.string,
      img: PropTypes.string,
      id: PropTypes.string.isRequired,
    })
  ).isRequired,
};

export default ShowcaseProjects;
